<template>
  <Container :title="title">
    <v-form @submit="submit">
      <template slot="base">
        <group :label="lang.name" type="input" v-model="arctype_mod.name" />
        <group :label="lang.operation" type="input" v-model="arctype_mod.operation" />
        <group :label="lang.sorts" type="input" v-model="arctype_mod.sorts" />
        <group :label="lang.status" type="status" v-model="arctype_mod.status" />
      </template>
    </v-form>
  </Container>
</template>

<script>
  import {Container,VForm,Group} from "@/components"
  export default {
    components: { Group, VForm,  Container },
    data(){
      return {
        title:this.$t('arctype_mod.editTitle'),
        advanced:true,
        lang:this.$t('arctype_mod'),
      }
    },
    computed:{
      arctype_mod:{
        set(value){
          this.$store.commit('arctype/SET_MODEL', value)
        },
        get(){
          return this.$store.state.arctype.model
        }
      }
    },
    created(){
      const mid = this.$route.query['id']
      this.$store.dispatch('arctype/fetchModel',mid)
    },
    methods:{
      submit(){
        this.$store.dispatch('arctype/editModel').then( resp =>{
          if (resp){
            this.$toast.success({
              title:'API 请求成功',
              message: resp.error
            })
          }
        })
      }
    }
  }
</script>



